}

@font-face {
  font-family: Avenir-Heavy;
  src: url("Avenir-Heavy.01d9ab74.ttf");
  font-weight: 900;
}

* {
  color: #3d4e61;
  box-sizing: border-box;
  font-family: Avenir-Book, sans-serif;
  font-style: normal;
  font-weight: 300;
}

.searchBarContainer__searchBar {
  margin-top: 20px;
  position: relative;
}

.searchBar {
  background: #fff;
}

.searchBar__input {
  border: 1px solid #e1e7ed;
  border-radius: 4px;
  width: 100%;
  box-shadow: 0 1px 8px #3d4e611a;
}

.searchBar__input--big {
  height: 70px;
  padding-left: 62px;
  font-size: 16px;
}

.searchBar__input--small {
  text-align: left;
  height: 42px;
  padding-left: 33px;
  font-size: 14px;
  font-weight: 400;
}

.searchBar--big:after {
  content: "";
  background-image: url("Search.ccd3c07b.svg");
  background-repeat: no-repeat;
  background-size: cover;
  width: 19px;
  height: 19px;
  position: absolute;
  top: 50%;
  left: 26px;
  transform: translateY(-50%);
}

.searchBar--small:after {
  content: "";
  background-image: url("Search.ccd3c07b.svg");
  background-repeat: no-repeat;
  background-size: cover;
  width: 11px;
  height: 11px;
  position: absolute;
  top: 50%;
  left: 13px;
  transform: translateY(-50%);
}

.searchBar__input:hover {
  box-shadow: 0 3px 8px #3d4e6133;
}

.searchBar__input:focus {
  background: linear-gradient(#fff 0%, #f6f6f7 100%);
  border: 1px solid #e1e7ed;
  outline: none;
